<template>
    <div class="mast" v-if="showchart && calculations.total>0"
    @click="handlecartshow"></div>
    <div class="cart">
        <div class="product" v-if="showchart && calculations.total >0">
            <div class="product__header">
                <div class="product__header__all"
                    @click="() => setcartchecked(shopid)">
                        <span class="product__header__icon iconfont"
                        v-html="calculations.allchecked ? '&#xe652;':'&#xe6f7;'"></span>
                        全选</div>
                    <div class="product__header__clear"
                >
                    <span class="product__header__clear__btn" @click="()=>clearcarproduct(shopid)">清空购物车</span> </div>

            </div>
            <div class="product__item"  
            v-for="item in productlist"
            :key="item._id">
                <div class="product__item__checked iconfont "
                v-html="item.check ? '&#xe652;':'&#xe6f7;'"
                @click="()=>changecaritemchanged(shopid,item._id)">
                    
                </div>
        <img class="product__item__img" :src="item.imgUrl" alt="">
        <div class="product__item__detail">
            <h4 class="product__item__title">{{item.name}}</h4>
            <p class="product__item__price">
                <span class="product__item__yen">&yen;</span>{{item.price}}
                <span class="product__item__yun"> &yen;{{item.oldprice}}</span>
            </p>
        </div>
        <div class="product__number">
            <span class="product__number__minus iconfont"
            @click="() => {changeiteminfo(shopid,item._id,item,-1)}"
            >&#xe691;</span>
             {{item.count || 0}}
            <span class="product__number__plus  iconfont"
           @click=" () => {changeiteminfo(shopid,item._id,item,1)}"
            >&#xe668;</span>
        </div>
    </div>
        </div>
        <div class="check">
            <div class="check__icon">
                <img src="http://www.dell-lee.com/imgs/vue3/basket.png" 
                class="check__icon__img"
                @click="handlecartshow"/>
                <div class="check__icon__tag">{{calculations.total}}</div>
            </div>
            <div class="check__info">总计:
                <span class="check__info__price">&yen;{{calculations.price}}</span>
            </div>
     
        <div class="check__btn" v-show="calculations.total>0" >
            <router-link :to="{path:`/orderconfire/${shopid}`}">
            去结算：
        </router-link>
        </div>
    </div>
    </div>
    
</template>

<script>
    import {  ref } from 'vue';
    import { useStore } from 'vuex';
    import { useRoute } from 'vue-router'
    import {usecomcarteffect} from '../../effects/carteffects'


    // 购物车信息逻辑
    const usecareffect =(shopid)=>{
        const {changeiteminfo,calculations,productlist} = usecomcarteffect(shopid)
        const store = useStore()
       
      
            const changecaritemchanged = (shopid,productid)=>{
                    store.commit('changecaritemchanged',{
                        shopid,productid
                    })
            }
            const clearcarproduct = (shopid) =>{
            store.commit('clearcarproduct',{shopid})
        }
            const setcartchecked = (shopid) =>{
            store.commit('setcartchecked',{shopid})
        }
            return {clearcarproduct,productlist,calculations,
              changeiteminfo,changecaritemchanged,setcartchecked}
    }
    const togoglecareffect = ()=>{
        const showchart = ref(false)
          const handlecartshow  =()=>{
            showchart.value = !showchart.value
          }
          return {showchart,handlecartshow}
    }
    
    export default{
          name:'car',
          setup(){
          const route = useRoute();
          const shopid = route.params.id
       
          const {productlist,
            changeiteminfo,changecaritemchanged,
            calculations,clearcarproduct,setcartchecked} = usecareffect(shopid)

            const {showchart,handlecartshow} = togoglecareffect()

            return{clearcarproduct,shopid,setcartchecked,showchart,
                calculations,productlist,changeiteminfo,changecaritemchanged,handlecartshow}
          
          }
    }
</script>

<style lang="scss" scoped>
      @import '../../style/mixins.scss';
      .mast{
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        background: rgba(0,0,0,.5);
        z-index: 1;
      }
    .cart{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
        background: #fff;
    }
    .product{
    overflow: scroll;
    flex: 1;
    background: #fff;
    &__header{
        display: flex;
        line-height: 0.52rem;
        border-bottom:#f1f1f1 .01rem;
        font-size: .14rem;
        color: #333;
        &__all{
            width: 0.64rem;
            margin-left: .18rem;
        }
        &__icon{
            vertical-align: top;
            display: inline-block;
            margin-right: .1rem;
            color: #0091ff;
            font-size: 0.2rem;
        }
        &__clear{
            flex: 1;
            margin-right: .16rem;
            text-align: right;
            &__btn{
                display: inline-block;
            }
        }
    }
    &__item{
        position: relative;
        display: flex;
        padding: .12rem 0;
        margin: 0 .16rem;
        border-bottom: .16rem;
        border-bottom: .01rem solid #999;
        &__checked{
            line-height: .5rem;
            margin-right: .2rem;
            color: #0091ff;
            font-size: .2rem;
        }
        &__img{
            width: 0.46rem;
            height: 0.46rem;
            margin-right: .16rem;
        }
        &__detail{
            overflow: hidden;
        }
        &__title{
            margin: 0;
            line-height: 0.2rem;
            font-size: .14rem;
            color: #333;
            @include ellipsis;
        }
        
        &__price{
            margin: .06rem  0 0 0;
            line-height: .16rem;
            font-size: .14rem;
            color: #e93b3b;
        }
        &__yen{
            font-size: .12rem;
        }
        &__yun{
            margin-left: .06rem;
            line-height: .2rem;
            font-size: .12rem;
            text-decoration: line-through;
            color:#999;
        }
        .product__number{
            position: absolute;
            right: 0;
            bottom: .26rem;
            &__minus,&__plus{
                display: inline-block;
                width:.2rem;
                height: .2rem;
                line-height: .16rem;
                border-radius:50%;
              
                font-size: .2rem;
                text-align: center;
            }
            &__minus{
               position: relative;
               top: .02rem;
                color: #666;
                margin-right: .05rem;
            }
            &__plus{
                position: relative;
               top: .02rem;
                color:#0091ff;
                margin-left:.05rem;
            }
        }
    }

}
    .check{
        display: flex;
        height: 0.49rem;
        border-top: solid .01rem #f1f1f1;
        line-height: .49rem;
        &__icon{
            position: relative;
            width: 0.84rem;
            &__img{
                display: block;
                margin: .12rem auto;
                width: 0.28rem;
                height: 0.26rem;
            }
            &__tag{
                top: .04rem;
                left: .46rem;
                position: absolute;
                min-width: 0.2rem;
                padding: 0 .04rem;
                height:0.2rem;
                line-height: .2rem;
                background-color: #e93823;
                border-radius: .1rem;
                font-size: .12rem;
                text-align: center;
                transform: scale(.5);
                color: #fff;
                transform-origin: left center;
            }
        }
        &__info{
            flex: 1;
            color: #333;
            font-size: .12rem;
            &__price{
                color: #e93823;
                font-size: 0.18rem;
                line-height: .49rem;
            }
        }
        &__btn{
            width: 0.98rem;
            text-align: center;
            background-color: #4fb0f9;
            color: #fff;
            font-size: .14rem;
            a{
                color: #333;
                text-decoration: none;
            }
        }
    }

</style>