<template>
    <div class="wrapper">
        <div class="search">
            <div class="search__back iconfont"
            @click="handbackclick">  &#xe6f2;</div>
            <div class="search__content">
                <span class="search__content__icon iconfont">&#xe62d;</span>
                <input class="search__content__input"
                placeholder="请输入商品名称"/>
            </div>
        </div>
        <shopinfo :item="item" :hideBorder="true " v-show="item.imgUrl"/>
        <Content :shopname="item.name"/>
        <cart />
    </div>
</template>

<script>
    import {reactive, toRefs} from 'vue'
    import { useRouter ,useRoute} from 'vue-router'
    import {get} from '../../utils/request'
    import shopinfo from '../../components/shopinfo.vue'
    import Content from './Content'
    import cart from './car'
    const useshopinfoeffect = () =>{
        const data = reactive({item:{}}) 
        const route = useRoute()       
        const getitemlist = async () =>{
        const result = await get(`/api/shop/${route.params.id}`)
        if(result?.errno===0 && result?.data){
             data.item = result.data
        }
    }
        const {item} = toRefs(data)
          return {item,getitemlist}
    }

    // 点击回退逻辑
    const usebackroutereffect = ()=>{
        const router = useRouter()  
        const handbackclick =()=>{
          router.back()
    }
    return handbackclick
}
   export default{
    name:'shop',
    components:{shopinfo,Content,cart},
    setup(){
        const {item,getitemlist} =useshopinfoeffect()
        const handbackclick=usebackroutereffect()
        getitemlist()
        return{item,handbackclick}
    }
}
</script>

<style lang="scss" scoped>
    .wrapper{
        padding:  0 .18rem;
    }
    .search{
        display: flex;
        margin: .14rem 0 .04rem 0;
        line-height:  .32rem;
      &__back{
        width: .3rem;
        font-size: .24rem;
        color:#b6b6b6;
      }
      &__content{
        display: flex;
        flex: 1;
        background: #f5f5f5;
        border-radius: .16rem;
        &__icon{
           width: .44rem;
          text-align: center;
           color: #b7b7b7;
        }
        &__input{
            display: block;
            width: 100%;
            padding-right: .2rem;
            border: none;
            outline: none;
            background: none;
            height: .32rem;
            font-size: .14rem;
            color: #333;
            &::placeholder{
                color: #333;
            }
        }
      }
    }

</style>