<template>
<div class="content">
<div class="category">
    <div 
    :class="{'category__item' : true,'category__item--active':currentab === item.tab}"
    v-for="item in categoryies"
    :key="item.name"
    @click="() =>handtabclick(item.tab)">
    {{item.name}}</div>
   </div>
   <div class="product">
    <div class="product__item" 
    v-for="item in list"
        :key="item._id">
        <img class="product__item__img" :src="item.imgUrl" alt="">
        <div class="product__item__detail">
            <h4 class="product__item__title">{{item.name}}</h4>
            <p class="product__item__sales">月售{{item.sales}}</p>
            <p class="product__item__price">
                <span class="product__item__yen">&yen;</span>{{item.price}}
                <span class="product__item__yun"> &yen;{{item.oldprice}}</span>
            </p>
        </div>
        <div class="product__number">
            <span class="product__number__minus iconfont"
            @click="() => {changecartitem(shopid,item._id,item,-1,shopname)}"
            >&#xe691;</span>
            {{getproductcount(shopid,item._id)}}
            
            <span class="product__number__plus iconfont"
           @click=" () => {changecartitem(shopid,item._id,item,1,shopname)}"
            >&#xe668;</span>
        </div>
    </div>
   </div>
</div>
</template>

<script>
    import { useRoute } from 'vue-router'
    import { reactive,toRefs,ref,watchEffect} from 'vue'
    import {get} from '../../utils/request'
    import {usecomcarteffect} from '../../effects/carteffects'
    import { useStore } from 'vuex'

    

    const categoryies =  [
        {name:'全部商品',tab:'all'},
        {name:'秒杀',tab:'seckill'},
        {name:'新鲜水果',tab:'fruit'},]

    const usetabeffect =()=>{
         const currentab = ref(categoryies[0].tab)
         const handtabclick = (tab)=>{ 
         currentab.value = tab
    }
    return {currentab,handtabclick}
    }

    // 列表内容相关的逻辑
    const usecurrentlisteffect = (currentab,shopid)=>{
        const content = reactive({
            list:[]
        })
        const getcontentdata = async ()=>{
        const result = await get (`/api/shop/${shopid}/products`,{
            tab:currentab.value})
        if(result?.errno=== 0 && result?.data?.length){
            content.list = result.data;
        }
    }
        watchEffect(()=>{getcontentdata()})
        const {list} = toRefs(content)
            return{list}
    }
// 购物车相关逻辑
    const usecareffect=()=>{
    const store = useStore()
    const {changeiteminfo,cartList} = usecomcarteffect()
    const changeshopname = (shopid,shopname)=>{
        store.commit('changeshopname',{shopid,shopname})

    }
    const changecartitem = (shopid,productid,item,num,shopname)=>{
        changeiteminfo(shopid,productid,item,num)
        changeshopname(shopid,shopname)
    }

    const getproductcount =(shopid,productid) =>{
     return cartList?.[shopid]?.productlist?.[productid]?.count || 0

    }
    return{cartList,changecartitem,getproductcount}
    }
    
export default{
   name:'Content',
   props:['shopname'],
   setup(){
    const route = useRoute()
    const shopid = route.params.id
   
    const {currentab,handtabclick} = usetabeffect()
    const {list} = usecurrentlisteffect(currentab,shopid)
    const {changecartitem,cartList,getproductcount} = usecareffect()

    return{list,categoryies, currentab,handtabclick,
       shopid,changecartitem,cartList,getproductcount}
   }
}
</script >

<style lang="scss" scoped>
    @import '../../style/mixins.scss';
.content{
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    top: 1.5rem;
    bottom: 0.5rem;
}
.category{
    width: 0.76rem;
    background: #f5f5f5;
    height: 100%;
    overflow-y: scroll;
    &__item{
        line-height: .4rem;
        text-align: center;
        font-size: .14rem;
        &--active{
            background: #fff;
        }
    }
}
.product{
    overflow: scroll;
    flex: 1;
    &__item{
        position: relative;
        display: flex;
        padding: .12rem 0;
        margin: 0 .16rem;
        border-bottom: .16rem;
        &__img{
            width: 0.68rem;
            height: 0.68rem;
            margin-right: .16rem;
        }
        &__detail{
            overflow: hidden;
        }
        &__title{
            margin: 0;
            line-height: 0.2rem;
            font-size: .14rem;
            color: #333;
            @include ellipsis;
        }
        &__slaes{
            margin: .06rem 0;
            font-size: .12rem;
            color: #333;
        }
        &__price{
            margin: 0;
            line-height: .16rem;
            font-size: .14rem;
            color: #e93b3b;
        }
        &__yen{
            font-size: .12rem;
        }
        &__yun{
            margin-left: .06rem;
            line-height: .2rem;
            font-size: .12rem;
            text-decoration: line-through;
            color:#999;
        }
        .product__number{
            position: absolute;
            right: 0;
            bottom: .12rem;
           line-height: 0.18rem;
            &__minus{
                position: relative;
               top: .02rem;
                color: #666;
                margin-right: .05rem;
            }
            &__plus{
                position: relative;
               top: .02rem;
              color:#0091ff;
                margin-left:.05rem;
            }
        }
    }

}
</style>